import React from 'react'
import slid1 from '../../Assets/images/slider-image-3.jpeg'
import slid2 from '../../Assets/images/slider-image-2.jpeg'
import slid3 from '../../Assets/images/slider-image-1.jpeg'
import blog1 from '../../Assets/images/blog-img-1.jpeg'
import blog2 from '../../Assets/images/blog-img-2.jpeg'

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function MainSlider() {
    var settings = {
        // dots: true,
        infinite: true,
        autoplay: true,
        speed: 5000,
        autoplaySpeed: 5000,
        cssEase: "linear",
        arrows:false
        // slidesToShow: 7,

      };
  return <>
<div className="container mt-1 mb-2">
<div className="row">
<div className="col-md-9">
     <Slider {...settings}>
   <img height='400px' className='w-100' src={slid1} alt="slid image" />
   <img height='400px' className='w-100' src={slid2} alt="slid image" />
   <img height='400px' className='w-100' src={slid3} alt="slid image" />
    </Slider>
     </div>
     <div className="col-md-3 cat d-photos ">     
   <img height='200px' className='w-100 ' src={blog1} alt="slid image" />
   <img height='200px' className='w-100' src={blog2} alt="slid image" />
     </div>
</div>
</div>
  </>
}
