import { useFormik } from 'formik'
import { CartContext } from '../../Context/CartContext';
import { useContext } from 'react';
export default function Address() {
    let {onlinePayment}=useContext(CartContext)
    async function handleAdressSubmit(values){
        let response=await onlinePayment(values)
        console.log(response?.data.session.url);
  window.location.href=response?.data.session.url;
    }
    let formik=useFormik({
        initialValues:{
          details:'',
            phone:'',
            city:''
        },
        onSubmit:handleAdressSubmit,
    })
  return <>
  <div className="container">
    <form onSubmit={formik.handleSubmit}>
        <label htmlFor="details">Details :</label>
        <input type="text" value={formik.values.details} onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control' name='details' id='details'/>

        <label htmlFor="phone">Phone :</label>
        <input type="tel" value={formik.values.phone} onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control' name='phone' id='phone'/>
        
        <label htmlFor="city">City :</label>
        <input type="text" value={formik.values.city} onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control' name='city' id='city'/>
         <button type='submit' className='btn bg-main text-white mt-2'>Pay Now</button>
    </form>
  </div>
  
  </>
}
