import axios from "axios";
import { Formik, useFormik } from "formik";
import React, { useContext, useState } from "react";
import { Audio } from 'react-loader-spinner'

import { Link, useNavigate } from "react-router-dom";
import * as yup from "yup";
import { userContext } from "../../Context/UserContext";
import { Helmet } from "react-helmet";
export default function Login() {
  let { setUserToken } = useContext(userContext);
  let Navigate = useNavigate();
  let [error, setError] = useState(null);
  let [isLoading, setLoading] = useState(false);
  let validationSceme = yup.object({
    email: yup.string().email().required("email is requird"),
    password: yup.string().required("password is requird"),
  });
  async function LoginSubmit(values) {
    setLoading(true);
    let { data } = await axios
      .post(`https://ecommerce.routemisr.com/api/v1/auth/signin`, values)
      .catch((err) => {
        setLoading(false);
        setError(err.response.data.message);
      });
    if (data.message === "success") {
      setLoading(false);
      localStorage.setItem("userToken", data.token);
      setUserToken(data.token);
      Navigate("/");
    }
  }
  let formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    validationSchema: validationSceme,
    onSubmit: LoginSubmit,
  });
  return (
    <>
     <Helmet>
        <title>Login</title>
    </Helmet>
      {error ? (
        <div className="alert alert-danger w-50 p-2 mt-2 mx-auto">{error}</div>
      ) : (
        ""
      )}
      <form
        className="mx-auto py-5 w-75 bg-light mt-5 px-5 d-flex flex-column justify-content-center "
        onSubmit={formik.handleSubmit}
      >
        <h2 className="mb-5 text-center">Login :</h2>
        <label htmlFor="email">E-mail</label>
        <input
          className="form-control w-75"
          type="email"
          name="email"
          id="email"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />
        {formik.errors.email && formik.touched.email ? (
          <div className="w-75 p-2 mt-2 alert alert-info">
            {formik.errors.email}
          </div>
        ) : (
          ""
        )}

        <label htmlFor="password">Password</label>
        <input
          className="form-control w-75"
          type="password"
          name="password"
          id="password"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />
        {formik.errors.password && formik.touched.password ? (
          <div className="w-75 p-2 mt-2 alert alert-info">
            {formik.errors.password}
          </div>
        ) : (
          ""
        )}
        {isLoading ?    <button disabled={!(formik.isValid && formik.dirty)} className="btn w-25  bg-main text-white mt-2" type="button">
              
              <Audio
                            height="20"
                width="90"
                color="white"
                ariaLabel="audio-loading"
                wrapperStyle={{}}
                wrapperClass="wrapper-class"
                visible={true}
              ></Audio>
                        </button> : (
          <button
            disabled={!(formik.dirty && formik.isValid)}
            type="submit"
            className="w-25  btn bg-main mt-2 text-white"
          >
            Login
          </button>
        )}
        <button className="btn text-white mt-2">
          <Link
            className="text-dark text-decoration-none mb-5"
            to={"/register"}
          >
            Register
          </Link>
        </button>
      </form>
    </>
  );
}
