import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../../Context/CartContext'
import { BallTriangle } from "react-loader-spinner";
import toast from "react-hot-toast";
import { useQuery } from 'react-query';
import { Link, useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet';
export default function Cart() {
  let {getLoggedUserCart,removeCartItem,updateProductQuantity,clearCart}=useContext(CartContext)
  let [cartDetails,setCartDetails]=useState(null)
  let Navigate=useNavigate()
 async function clearCartBridge(){
    let message=await clearCart()
  alert('Cart is empty now...!')
  Navigate('/')
  }

  async function updateCount(productId,count){
     let{data}=await updateProductQuantity(productId,count)
     setCartDetails(data)
     
  }
 async function removeItem(productId){
   let {data}=await removeCartItem(productId)
   setCartDetails(data)
 }
 async function getCart(){
    let {data}=await getLoggedUserCart()
    console.log(data)
    setCartDetails(data)
  }
  useEffect(()=>{
    getCart()
  },[])
  return<>
   <Helmet>
        <title>Cart</title>
    </Helmet>
  {cartDetails?
  <div className='w-75 mx-auto p-3 bg-main-light'>
  <h3>shopping Cart</h3>
  <h4 className='h6 text-main fw-bolder'>Cart Items : {cartDetails.numOfCartItems}</h4>
  <h4 className='h6 text-main fw-bolder'>Total Cart Price : {cartDetails.data.totalCartPrice} EGP</h4>
    {cartDetails.data.products.map((product)=><div key={product.product.id} className='row border-bottom py-2 mt-4'>
      <div className='col-md-1'>
        <img className='w-100' src={product.product.imageCover} alt="" />
      </div>
      <div className="col-md-11">
        <div className="d-flex justify-content-between align-items-center">
           <div>
            <h3 className='h6'>{product.product.title.split(' ').slice(0,2).join('')}</h3>
            <h6 className='text-main'>Price:{product.price} EGP</h6>
           </div>
        <div>
        <button onClick={()=>updateCount(product.product.id,product.count+1 )} className='btn brdr-main p-1 '>+</button>
           <span className='mx-2'>{product.count}</span>
           <button className='btn brdr-main p-1 ' onClick={()=>updateCount(product.product.id,product.count-1 )}>-</button>

        </div>
           
           
        </div>
        <button onClick={()=>removeItem(product.product.id)} className='btn p-0  font-sm'><i className='fas fa-trash-can text-danger'></i> Remove</button>
      </div>
    </div>)}
  <div className="pay d-flex justify-content-between">
    <Link className='text-white text-center rounded-2 btn w-25 bg-main' to={'/address'}>Online Payment</Link>
    <button className='text-white mt-2 rounded-2 w-25 bg-main'>Cash on delevery</button>

    
  </div>
    <div className='d-flex justify-content-center' >
  <button onClick={()=>clearCartBridge()} className="btn btn-danger mt-3">Clear Cart  <i className='fas fa-trash-can text-white'></i></button>
</div>
 </div>:<section id='loading' className='d-flex align-items-center justify-content-center mt-5 '>

 <BallTriangle
              height={100}
              width={100}
              radius={5}
              color="#4fa94d"
              ariaLabel="ball-triangle-loading"
              wrapperStyle={{}}
              wrapperClass=""
              visible={true}
              />
 </section>
  
}


  </>
}
