import axios from "axios";
import { useFormik } from "formik";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Audio } from 'react-loader-spinner'
import {Helmet} from "react-helmet";
import * as yup from "yup";
export default function Register() {
  let Navigate=useNavigate()
  const [error,setError]=useState(null)
  const [isLoading,setLoading]=useState(false)
 async function registerSubmit(values) {
  setLoading(true)
  let {data}=await  axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`,values)
  .catch((err)=>{
    setLoading(false)
    setError(err.response.data.message)})
  if(data.message==='success'){
    setLoading(false)
    Navigate('/login')
  }
    console.log(data);
  }
 
  let phoneRegex=/^\s*(?:\+?(\d{1,3}))?[-. (]*(\d{3})[-. )]*(\d{3})[-. ]*(\d{4})(?: *x(\d+))?\s*$/
  let validateSceme = yup.object({
    name: yup.string().min(3, "min char is 3").max(10, 'max is 10').required('name is requird'),
    email: yup.string().email('email is in valid ').required('email is requird'),
    phone:yup.string().matches(phoneRegex,'phone is invalid').required('phone is requird'),
    password:yup.string().matches(/^[A-Z][a-z0-9]{5,10}$/,'password is invalid').required('password is requirrd'),
    rePassword:yup.string().oneOf([yup.ref("password")],'pasword and rePassword not matching') .required('password is requird')
  });
  let formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      phone: "",
      password: "",
      rePassword: "",
    },
    validationSchema:validateSceme,
    onSubmit: registerSubmit,
  });
  return (
    <>
  
 
    {error?<div className="alert alert-danger w-50 mt-2 mx-auto" >{error}</div>:''
    }
      <form className="bg-light px-5 mt-3 mx-auto py-5 w-75" onSubmit={formik.handleSubmit}>
    <h2 className="mb-3 text-center">Register Now :</h2>
        <label htmlFor="name">Name</label>
        <input
          type="text"
          name="name"
          id="name"
          className="form-control w-75"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />
        {formik.errors.name && formik.touched.name ? (
          <div className="alert alert-info w-75 mt-2 py-2">
            {formik.errors.name}
          </div>
        ) : (
          ""
        )}

        <label htmlFor="email">E-mail</label>
        <input
          type="email"
          name="email"
          id="email"
          className="form-control w-75"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />
        {formik.errors.email && formik.touched.email ? (
          <div className="alert alert-info mt-2 py-2 w-75">
            {formik.errors.email}
          </div>
        ) : (
          ""
        )}
        <label htmlFor="phone">Phone</label>
        <input
          type="tel"
          name="phone"
          id="phone"
          className="form-control w-75"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />
        {formik.errors.phone && formik.touched.phone ? (
          <div className="alert alert-info w-75 py-2">
            {formik.errors.phone}
          </div>
        ) : (
          ""
        )}
        <label htmlFor="password">Password</label>
        <input
          type="password"
          name="password"
          id="password"
          className="form-control w-75"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />
         {formik.errors.password && formik.touched.password ? (
          <div className="alert alert-info w-75 py-2">
            {formik.errors.password}
          </div>
        ) : (
          ""
        )}
        <label htmlFor=" rePassword">RePassword</label>
        <input
          type="password"
          name="rePassword"
          id="rePassword"
          className="form-control w-75"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />
         {formik.errors.rePassword && formik.touched.rePassword ? (
          <div className="alert alert-info w-75 py-2">
            {formik.errors.rePassword}
          </div>
        ) : (
          ""
        )}
        {isLoading?
            <button disabled={!(formik.isValid && formik.dirty)} className="btn bg-main text-white mt-2" type="button">
              
<Audio
              height="20"
  width="90"
  color="white"
  ariaLabel="audio-loading"
  wrapperStyle={{}}
  wrapperClass="wrapper-class"
  visible={true}
></Audio>
          </button>:
        <button disabled={!(formik.isValid && formik.dirty)} className="btn bg-main text-white mt-2" type="submit">
          Register
        </button>
      }
    
      </form>
    </>
  );
}
