import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useQuery } from "react-query";
import { useNavigate, useParams } from "react-router-dom";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import {CartContext} from '../../Context/CartContext'
import {userContext} from '../../Context/UserContext'
import toast from "react-hot-toast";
export default function ProductDetails() {
  let params = useParams();
  let {userToken}=useContext(userContext)
  let {AddCart}=useContext(CartContext)
  let Navigate=useNavigate()
  async function addProductToCart(productId){
    if(userToken==null){
      Navigate('/login')
    }else{
      let response =await AddCart(productId)                                                 // ليس الا كوبري هعم كول فيها للكارت
      if(response.data.status=='success'){
       toast.success('Product Added Successfully',{
         duration:2000,
       })
      }else{
       toast.error('Try Again Next Time')
      }
    }
  }
  function getProductDetails(id) {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`);
  }
  let { isLoading, data, isError } = useQuery("productDetails", () =>

    getProductDetails(params.id)
  );
  

  var settings = {
    dots: true,
    infinite: true,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 2000,
    cssEase: "linear",

  };
  return (
    <>
    
      {data?.data.data ? (
        <div className="container py-3" >
          <div className="row  align-align-items-lg-stretch">
          <Helmet>
                <meta charSet="utf-8" />
                <title>{data?.data.data.title}</title>
                <link rel="canonical" href="http://mysite.com/example" />
            </Helmet>
            <div className="col-md-4">
            <Slider {...settings}>
  {data?.data.data.images.map((img)=> <img key={img}  src={img} alt={data?.data.data.title}/>)}
  </Slider>
            </div>
            <div className="col-md-8 mt-5  ">
                <h2 className="h5">{data.data.data.title}</h2>
                <p>{data?.data.data.description}</p>
                <h6 className="text-main">{data?.data.data.category.name}</h6>
                <h6 className="text-main">Price : {data?.data.data.price} EGP</h6>
<div className="d-flex justify-content-between">
    <span>RatingsQuantity : {data?.data.data.ratingsQuantity}</span>
        <span><i className="fa fa-star rating-color"></i>{data?.data.data.ratingsAverage}</span>
</div>
<button className="btn bg-main w-100 text-white mt-2 " onClick={()=>addProductToCart(data?.data.data.id)}>Add to Cart</button>
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
    </>
  );
}

// use params is hook in react router dom بترجع البارامتر االي باعتها ف url
