import React, { useContext, useEffect, useState } from 'react'
import { WishlistContext } from '../../Context/WishlistContext'
import { CartContext } from '../../Context/CartContext'
import { useNavigate } from 'react-router-dom'
import toast from 'react-hot-toast'
import { userContext } from '../../Context/UserContext'
export default function WhisList() {
  let {getLoggedCart,removeWhisList}=useContext(WishlistContext)
  let [wishProduct,setWhishProduct]=useState(null)
  let {AddCart}=useContext(CartContext)
  let {userToken}=useContext(userContext)

let Navigate=useNavigate()
  async function addProductToCart(productId){
    if(userToken==null){
      Navigate('/login')
    }else{
      let response =await AddCart(productId)                                                 // ليس الا كوبري هعم كول فيها للكارت
      if(response.data.status=='success'){
       toast.success('Product Added Successfully',{
         duration:2000,
       })
      }else{
       toast.error('Try Again Next Time')
      }
    }
  
  }
 async function whishLoggedBridge(){
    let {data}=await getLoggedCart()
    setWhishProduct(data)
  }
  useEffect(()=>{
    whishLoggedBridge()
  },[])
  async function removeItem(productId){
    let x=await removeWhisList(productId)
    whishLoggedBridge()
    
  }
  return <> <div className='bg-light mt-5 container'>
  {wishProduct? <div>
    <h1>count : {wishProduct.count}</h1>
   {wishProduct.data.map((prod)=> <div key={prod.id} className='row bg-light justify-content-center mt-5'>
    <div className="col-md-4" height="100px">
      <img src={prod.imageCover} className='w-100 h-75 shadow' alt="" />
    </div>
<div className="col-md-7 py-3 ">
  <h2 className='text-main mt-2'>{prod.title.split(" ").slice(0,2).join("")}</h2>
<h3 className='mt-4'>{prod.price}</h3>
<h4 className='mt-3'>{prod.description.split(' ').slice(0,10).join(' ')}</h4>
<button onClick={function(){removeItem(prod.id)}} className='btn btn-danger d-block'>Remove <i className='fas fa-trash'></i></button>
<button onClick={()=>addProductToCart(prod.id)} className='btn bg-main text-white w-100 mt-5 '>Add To Cart</button>
  </div>    
   </div>)}
  </div>
  :'Loading....'}
  </div>
  </>
}
