import axios from 'axios';
import React from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { useQuery } from 'react-query';
import Product from '../Product/Product';

export default function CategorySlider() {
    function getFeaturedProducts() {
        return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`);
    }
    let { isLoading, isError, data, isFetching } = useQuery(
        "categories",
        getFeaturedProducts,
        {
            // cacheTime:3000  // مدة بقاء الكاش
            // refetchOnMount:false
            // staleTime:30000   // الداتا القديمة تفضل معروضة اد اي
            // refetchInterval:1000         // اسعار الدهب مثلا -كل اد اي اجيب الداتا
        }
        );
        console.log(data?.data.data)
        var settings = {
            // dots: true,
            infinite: true,
            autoplay: true,
            speed: 2000,
            autoplaySpeed: 2000,
            cssEase: "linear",
            slidesToShow: 7,
            // rows: 2,
          };
  return (
 <div className="container mt-3 mb-2">
    <h2 className='h3'>Shop Popular Categories</h2>
       <Slider {...settings}>
  {data?.data.data.map((category)=> <img key={category} height={100} src={category.image}/>)}
  </Slider>
 </div>
  )
}
