import React, { useContext, useEffect } from "react";
import Layout from "./components/Layout/Layout";
import Navbar from "./components/Navbar/Navbar";
import Footer from "./components/Footer/Footer";
import Home from "./components/Home/Home";
import Cart from "./components/Cart/Cart";
import About from "./components/About/About";
import Category from "./components/Category/Catgory";
import Brands from "./components/Brands/Brands";
import Product from "./components/Product/Product";
import Notfound from "./components/Notfound/Notfound";
import Login from "./components/Login/Login";
import Register from "./components/Register/Register";
import { RouterProvider, createBrowserRouter, createHashRouter } from "react-router-dom";
import CounterContextProvider from "./Context/CounterContext";
import { userContext } from "./Context/UserContext";
import ProtectedRoute from "./components/ProtectedRoute/Protected";
import ProductDetails from "./components/productDetails/ProductDetails";
import CartContextProvider from "./Context/CartContext";
import  { Toaster } from 'react-hot-toast';
import WishlistProvbider, { WishlistContext } from "./Context/WishlistContext";
import WhisList from "./components/WhishList/WhisList";
import Profile from "./components/Profile/Profile";
import Address from "./components/Address/Address";
import Orders from "./components/Orders/Orders";
let router = createHashRouter([
  {
    path: "/",
    element: <Layout></Layout>,
    children: [
      { index: true, element: <Home></Home> },
      {
        path: "navbar",
        element: (
          <ProtectedRoute>
            <Navbar></Navbar>
          </ProtectedRoute>
        ),
      },
      {
        path: "product",
        element: (
          <ProtectedRoute>
            <Product></Product>
          </ProtectedRoute>
        ),
      },
      { path: "login", element: <Login></Login> },
      { path: "register", element: <Register></Register> },
      { path: "footer", element: <Footer></Footer> },
      {
        path: "Cart",
        element: (
          <ProtectedRoute>
            <Cart></Cart>
          </ProtectedRoute>
        ),
      },
      {
        path: "Brands",
        element: (
          <ProtectedRoute>
            <Brands></Brands>
          </ProtectedRoute>
        ),
      },
      {
        path: "About",
        element: (
          <ProtectedRoute>
            <About></About>
          </ProtectedRoute>
        ),
      },
      {
        path: "ProductDetails/:id",
        element: (
          <ProtectedRoute>
            <ProductDetails></ProductDetails>
          </ProtectedRoute>
        ),
      },
      {
        path: "Category",
        element: (
          <ProtectedRoute>
            <Category></Category>
          </ProtectedRoute>
        ),
      },
      {
        path: "Profile",
        element: (
          <ProtectedRoute>
            <Profile></Profile>
          </ProtectedRoute>
        ),
      },
      {
        path: "whishList",
        element: (
          <ProtectedRoute>
            <WhisList></WhisList>
          </ProtectedRoute>
        ),
      },
      {
        path: "address",
        element: (
          <ProtectedRoute>
            <Address></Address>
          </ProtectedRoute>
        ),
      },
      {
        path: "allorders",
        element: (
          <ProtectedRoute>
            <Orders></Orders>
          </ProtectedRoute>
        ),
      },
      
      { path: "*", element: <Notfound></Notfound> },
    ],
  },
]);
export default function App() {
  let { setUserToken } = useContext(userContext);
  useEffect(() => {
    if (localStorage.getItem("userToken") !== null) {
      
      setUserToken(localStorage.getItem("userToken"));
    }
  }, []);

  return (
    <CartContextProvider>
<WishlistProvbider>
      <CounterContextProvider>
        <RouterProvider router={router}></RouterProvider>
      </CounterContextProvider>
      <Toaster/>
      </WishlistProvbider>
     </CartContextProvider>
  );
}
