import axios from "axios";
import { createContext } from "react";
export let CartContext = createContext();
//
let usreToken = localStorage.getItem("userToken");
let header = {
  token: usreToken,
};
function AddCart(productId) {
  return axios
    .post(
      `https://ecommerce.routemisr.com/api/v1/cart`,
      {
        productId: productId,
      },
      {
        headers: header,
      }
    )
    .then((response) => response)
    .catch((error) => error('try again'));
}
function getLoggedUserCart() {
  return axios
    .get(`https://ecommerce.routemisr.com/api/v1/cart`, {
      headers: header,
    })
    .then((response) => response)
    .catch((error) => error);
}
function removeCartItem(productId){
  return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`,
  {
headers:header
  }).then((response)=>response)
  .catch((error)=>error)
}
function updateProductQuantity(productId,count){
 return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`,
  {
    count:count    
  },
  {
    headers:header
  }
  ).then((response)=>response)
  .catch((error)=>error)
}
function onlinePayment(values){
  return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/6739f631803e888e05256134?url=http://localhost:3000`,
   {
    shippingAddress:values   
   },
   {
     headers:header
   }
   ).then((response)=>response)
   .catch((error)=>error)
 
 }
function clearCart(){
  return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/`,
  {
    headers:header
  }
  ).then((response)=>response)
  .catch((error)=>error)
}
export default function CartContextProvider(props) {
  return (
    <CartContext.Provider value={{ AddCart, getLoggedUserCart,removeCartItem,updateProductQuantity,clearCart,onlinePayment}}>
      {props.children}
    </CartContext.Provider>
  );
}
