import React, { useContext, useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import logo from "../../Assets/images/freshcart-logo.svg";
import { userContext } from "../../Context/UserContext";
import { CartContext } from "../../Context/CartContext";
import { useQuery } from "react-query";
export default function Navbar() {
  let {getLoggedUserCart}=useContext(CartContext)
  
  let navigate=useNavigate()
  let { userToken, setUserToken } = useContext(userContext);
  let [cartDetails,setCartDetails]=useState(null)
  async function getCart(){
    let {data}=await getLoggedUserCart()
    console.log(data)
    setCartDetails(data)
  }
  let {data}=useQuery('dispNum',getCart)

  function logout(){
    localStorage.removeItem('userToken')
    setUserToken(null)
    navigate('/login')
  }
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid">
          <img src="" alt="" />
          <Link className="navbar-brand fw-bolder" to="">
            <img src={logo} alt="fresh market logo" />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <li className="nav-item navbar-nav">
                    <Link className="nav-link active fw-bold" aria-current="page" to="">
                    <i className="fa fa-solid fa-home text-main"></i> Home
                    </Link>
                  </li>
            {userToken !== null ? (
              <>
              
                <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                
                  <li className="nav-item">
                    <Link
                      className="nav-link active"
                      aria-current="page"
                      to="cart"
                    >
                   <i className="fa-solid fa-cart-plus fw-bold" ></i>
                      <span className="text-main fw-bolder"> {cartDetails?.numOfCartItems}</span>
                    </Link>
                  </li>

                  <li className="nav-item">
                    <Link
                      className="nav-link active"
                      aria-current="page"
                      to="Category"
                    >
                      Catgories
                    </Link>
                  </li>
                  <li className="nav-item">
                   
                  </li>
                  <li className="nav-item">
                    <Link
                      className="nav-link active"
                      aria-current="page"
                      to="brands"
                    >
                      Brands
                    </Link>
                  </li>
               
                  
                </ul>
                <ul className="navbar-nav ms-0 ">
                <li className="nav-item">
                    <Link
                      className="nav-link active"
                      aria-current="page"
                      to="whishList"
                    >
                     <i className="fa-solid fa-heart text-danger fs-5"></i>
                    </Link>
                  </li>
                </ul>
              </>
            ) : (
              ""
            )}

            <ul className="navbar-nav ms-auto mb-2 mb-lg-0 d-fle justify-content-around align-items-center">
              <div>
           
              </div>
            <div>
            <li className="nav-item d-flex align-items-center cursor-pointer">
                <i className="fab fa-facebook mx-2"></i>
                <i className="fab fa-youtube mx-2"></i>
                <i className="fab fa-twitter mx-2"></i>
              </li>
              
            </div>
              {userToken !== null ? (
                <>
                  <span className="cursor-pointer nav-link active" aria-current="page" onClick={()=>logout()}>
                    Logout
                  </span>
                </>
              ) : (
                <>
                  <li className="nav-item">
                    <Link
                      className="nav-link active"
                      aria-current="page"
                      to="login"
                    >
                     <i className="fa-solid fa-arrow-right-to-bracket text-main"></i> login
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link
                      className="nav-link active"
                      aria-current="page"
                      to="register"
                    >
                      Register
                    </Link>
                  </li>
                  <li className="nav-item"></li>
                </>
              )}
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
