import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { BallTriangle } from "react-loader-spinner";
import { useQuery } from "react-query";
import { Link, useNavigate } from "react-router-dom";
import { CartContext } from "../../Context/CartContext";
import toast from "react-hot-toast";
import { userContext } from "../../Context/UserContext"; 
import {WishlistContext} from '../../Context/WishlistContext'
export default function FeaturedProduct() {
  let {userToken}=useContext(userContext)
  let {AddCart}=useContext(CartContext)
  let {addWishlist}=useContext(WishlistContext)
  let Navigate=useNavigate()
  async function addListBridge(productId){
    if(userToken==null){
      Navigate('/login')
    }else{
      let response=await addWishlist(productId)
      if (response.data.status=='success'){
        toast.success(response.data.message,{
          duration:2000,
        })
      }else{
       toast.error('Try Again Next Time')

      }
    }
  }
  async function addProductToCart(productId){
    if(userToken==null){
      Navigate('/login')
    }else{
      let response =await AddCart(productId)                          
      if(response.data.status=='success'){
       toast.success('Product Added Successfully',{
         duration:2000,
       })
      }else{
       toast.error('Try Again Next Time')
      }
    }
  
  }
  function getFeaturedProducts() {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products`);
  }
  let { isLoading, isError, data, isFetching } = useQuery(
    "featuredProduct",
    getFeaturedProducts,
  
  );



  return (
    <div className="container mt-5">
      <div className="row">
        {isLoading ? (
          <div className="text-center mt-5 py-5 d-flex justify-content-center align-items-center mb-5">
            <BallTriangle
              height={100}
              width={100}
              radius={5}
              color="#4fa94d"
              ariaLabel="ball-triangle-loading"
              wrapperStyle={{}}
              wrapperClass=""
              visible={true}
            />
          </div>
        ) : (
          data.data.data.map((product) => (
            <div key={product.id} className="col-md-2 col-sm-10 xxx">
              <div className="product p-3 cursor-pointer">
                <div className="position-relative ">
                  <i  onClick={()=>addListBridge(product.id)} className="fa-regular fa-heart fs-3  like"></i>
                  <Link to={`/productDetails/${product.id}`}>
                    <img src={product.imageCover} className="w-100" alt="" />
                    <p className="text-main">{product.category.name}</p>
                    <p>{product.title.split(" ").slice(0, 2).join("")}</p>
                    <div className="product-box d-flex justify-content-between"></div>
                    <span>{product.price}EGP </span>
                    <span>
                      <i className="fa fa-solid fa-star rating-color"></i>{" "}
                      {product.ratingsAverage}
                    </span>
                  </Link>
                </div>
                <button onClick={()=>addProductToCart(product.id)}
                  className=" btn bg-main text-white w-100 mt-2"
                >
                  Add to Cart
                </button>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
}