import React, { useContext, useEffect, useState } from 'react'
import { OrderContext } from '../../Context/OrderContext'
import { jwtDecode } from 'jwt-decode'

export default function Orders() {
    let [ordersDetails,setOrdersDetails]=useState('')
    let {getOrders}=useContext(OrderContext)
    let encodedToken=   localStorage.getItem('userToken')
    let decodedToken=jwtDecode(encodedToken)
    let userId=decodedToken.id
    async function displayOrders(){
        let res=await getOrders(userId)
        console.log(res.data);
        setOrdersDetails(res)

      }
      console.log(ordersDetails);
    useEffect(()=>{
        displayOrders()
    },[])
  return <>
      <div className="container">
        <div className="row ">
  {ordersDetails? <div>
    <h2>Orders</h2>
    <div className="container">
      <div className="row">
      {ordersDetails?.data.map((prod)=><div  key={prod.id} className='col-md-2' >
            {prod.cartItems.map((item)=><div key={prod.id}>
              <img className='w-100' src={item.product.imageCover} alt="" />

            </div>)}
    </div>)}
      </div>
    </div>
  
            </div>  
      
      
      
      :'' }
      </div>
      </div>
 
  </>
}
