import axios from "axios";
import React from "react";
import { Helmet } from "react-helmet";
import { useQuery } from "react-query";

export default function Catgory() {

  function getCategories() {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`);
  }
  let { isLoading, data, isError } = useQuery("categories", getCategories);
  console.log(data?.data.data);

  return (
    <>
       <Helmet>
        <title>Category</title>
    </Helmet>
      <div className="container">
        <div className="row py-3">
          {isLoading ? (
            <div>
              <h2>Loading</h2>
            </div>
          ) : (
            data.data.data.map((product)=> <div key={product.id} className="col-md-3 product p-3 cursor-pointer">
                <img src={product.image} className="w-100 h-50" alt="" />
                <h3 className="text-center">{product.name}</h3>
            </div> )
          )}
        </div>
      </div>
    </>
  );
}
